/* -- Forms -- */

input,
select,
textarea {
	border-radius: 0;
	color: var(--wp--preset--color--black);
	font-size: var(--wp-wp--preset--font-size--xs);
	margin-top: 0.5rem;
	padding: 0.625rem 1rem;
	width: 100%;
}

input,
textarea {
	border: 1px solid var(--wp--preset--color--gray-200);
}

select {
	appearance: none;
	border: none;
	outline: 0;
	background: url(../images/icons/icon-arrow-down.svg) no-repeat right 11px
		center / 1.4em;

	&.wp-block-search__filters {
		background: url(../images/icons/icon-arrow-down-white.svg) no-repeat right 11px
		center / 1.4em,
		linear-gradient(
			to right, 
			var( --wp--preset--color--contrast-3 ), 
			var( --wp--preset--color--contrast-3 ) calc( 100% - 42px ), 
			var( --wp--preset--color--contrast-4 ) 43px, 
			var( --wp--preset--color--contrast-4 )
		); /* stylelint-disable-line function-comma-space-after */
	}

	cursor: pointer;

	&::-ms-expand {
		display: none;
	}

	&:focus {
		outline: none;
	}

	option {
		background-color: var(--wp--preset--color--white);
		color: inherit;
	}
}

input[type='checkbox'],
input[type='image'],
input[type='radio'] {
	width: auto;
}

input[type='button'],
input[type='email'],
input[type='search'],
input[type='submit'],
input[type='text'],
textarea {
	-webkit-appearance: none;
	appearance: none;
}

input[type='submit'] {
	border: none !important; /* stylelint-disable-line declaration-no-important */
	border-radius: 4px;
	padding: 0.625rem 1.125rem;
}

input:focus,
textarea:focus {
	outline: none;
}

::placeholder {
	color: var(--wp--preset--color--gray-500);
}

label {
	color: var(--wp--preset--color--black);
	display: inline-block;
	font-weight: 700;
	letter-spacing: 0.032px;
}
